img{
    width: auto;
    height: auto;
    margin: 0;
    padding: 0;
    object-fit: cover;
    border: none;
}

@media all and (min-width:1200px) {
    .banner{
        height: 700px;
    }

    .banner img{
        height: 700px;
    }

    .backImg{
        width: 100%;
    }

    .moon{
        width: 700px;
    }

}

@media (max-width:1199px) and (min-width:760px) {
    .banner{
        height: 390px;
    }

    .banner img{
        height: auto !important;
    }

    .moon{
        width: 430px;
    }
}

@media (max-width:759px) {
    .banner{
        height: 200px;
    }
    .moon{
        width: 270px;
    }
}


.banner{
    width: 100%;
    /* background-image: url('../img/brick.png'); */
    /* background-image: url('../img/main.png'); */
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: cover;
    position: relative;
}

.banner img{
    position: absolute;
    z-index: -1;
}

.backImg{
        width: 100%;
}

@keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}

.moon{
    animation: rotate 200s linear infinite; /* 회전 애니메이션 적용 */
}

.banner span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 5em; /* 크기 조정 */
    font-weight: bold;
    z-index: 1; /* 다른 요소 위로 올리기 */
  }
  
  .banner p {
    font-size: 1em; /* 크기 조정 */
    margin-top: 100px; /* 제목과의 간격 조정 */
  }

/* 키 프레임 왜 안먹음? */
@-webkit-keyframes Ne {
    0% {
        color: #fff;
        text-shadow: 0px 0px 20px #00aad4;
    }

    19% {
        color: #fff;
        text-shadow: 0px 0px 20px #00aad4;
    }

    20% {
        color: #9E9E9E;
        text-shadow: none;
    }

    21% {
        color: #fff;
        text-shadow: 0px 0px 20px #00aad4;
    }

    60% {
        color: #fff;
        text-shadow: 0px 0px 20px #00aad4;
    }

    61% {
        color: #9E9E9E;
        text-shadow: none;
    }

    62% {
        color: #fff;
        text-shadow: 0px 0px 20px #00aad4;
    }

    63% {
        color: #9E9E9E;
        text-shadow: none;
    }

    64% {
        color: #fff;
        text-shadow: 0px 0px 20px #00aad4;
    }

    100% {
        color: #fff;
        text-shadow: 0px 0px 20px #00aad4;
    }
}

@-webkit-keyframes NeAfter {
    0% {
        color: #154fbe;
    }

    19% {
        color: #154fbe;
    }

    20% {
        color: transparent;
    }

    21% {
        color: #154fbe;
    }

    60% {
        color: #154fbe;
    }

    61% {
        color: transparent;
    }

    62% {
        color: #154fbe;
    }

    63% {
        color: transparent;
    }

    64% {
        color: #154fbe;
    }

    100% {
        color: #154fbe;
    }
}

@-webkit-keyframes NeBefore {
    0% {
        background: #154fbe;
    }

    19% {
        background: #154fbe;
    }

    20% {
        background: transparent;
    }

    21% {
        background: #154fbe;
    }

    60% {
        background: #154fbe;
    }

    61% {
        background: transparent;
    }

    62% {
        background: #154fbe;
    }

    63% {
        background: transparent;
    }

    64% {
        background: #154fbe;
    }

    100% {
        background: #154fbe;
    }
}

.textNe {
    font-family: 'Pacifico', cursive;
    font-weight: 100;
    margin: auto;
    color: #fff;
    font-size: 50px;
    text-shadow: 0px 0px 20px #154fbe;
    -webkit-animation: Ne 5s infinite;
    animation: Ne 5s infinite;
    text-align: center;
    line-height: 1;
    position: relative;
}

.textNe:after {
    width: 100%;
    height: 100%;
    content: attr(data-text);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #154fbe;
    filter: blur(15px);
    -webkit-animation: NeAfter 5s infinite;
    animation: NeAfter 5s infinite;
    z-index: -0.5;
}

.textNe:before {
    width: 100%;
    height: 100%;
    background: #154fbe;
    opacity: 0.7;
    filter: blur(50px);
    -webkit-animation: NeBefore 5s infinite;
    animation: NeBefore 5s infinite;
    z-index: -0.5;
}

